<template>
	<el-row>
		<!-- 中栏 -->
		<el-col :span='7' class='pos-order' id='order-list'>
			<!-- 选项卡组件 -->
			<el-tabs class='mid-tab'>
				<!-- 一个选项卡 -->
				<el-tab-pane label="点餐">
					<!-- 列表 -->
					<el-table :data='tableData' border style='width: 100%'>
						<!-- 一列[对应数据的key,表头名称] -->
						<el-table-column prop='goodsName' label='商品名称'></el-table-column>
						<el-table-column prop='count' label='数量' width='50'></el-table-column>
						<el-table-column prop='price' label='金额' width='70'></el-table-column>
						<el-table-column label='操作' width='100' fixed='right'>
							<!-- 列表中固定的按钮 -->
							<template slot-scope='scope'>
								<el-button type='text' size='small' @click='delSingleGoods(scope.row)'>删除</el-button>
								<el-button type='text' size='small' @click='addOrderList(scope.row)'>增加</el-button>
							</template>
						</el-table-column>
					</el-table>
					<!-- 统计金额 -->
					<div class="total-div">
						<small>数量: </small>{{totalCount}}个
						&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
						<small>金额: </small>￥{{totalMoney}}
					</div>
					<!-- 下方三个按钮 -->
					<div class="div-btn">
						<el-button type='warning'>挂单</el-button>
						<el-button type='danger' @click='delAllGoods'>清空</el-button>
						<el-button type='success' @click='checkOut'>结账</el-button>
					</div>
				</el-tab-pane>
				<el-tab-pane label="挂单">
					挂单
				</el-tab-pane>
				<el-tab-pane label="外卖">
					外卖
				</el-tab-pane>
			</el-tabs>
		</el-col>
		<!-- 右栏 -->
		<el-col :span='17'>
			<!-- 上方 - 热销商品 -->
			<div class="great-goods">
				<div class="title">热销商品</div>
				<!-- 商品列表 -->
				<div class="great-goods-list">
					<ul>
						<li v-for='goods in greatGoods' @click='addOrderList(goods)'>
							<span>{{goods.goodsName}}</span>
							<span class="o-price">￥{{goods.price}}元</span>
						</li>
					</ul>
				</div>
			</div>
			<!-- 下方 - 商品分类菜单 -->
			<div class="goods-type">
				<el-tabs type='card'>
					<!-- 分类 -->
					<el-tab-pane label='汉堡'>
						<!-- 商品列表 -->
						<div>
							<ul class="cook-list">
								<li v-for='goods in type0Goods' @click='addOrderList(goods)'>
									<span class="food-img"><img :src="goods.goodsImg" width="100%"></span>
									<span class="food-name">{{goods.goodsName}}</span>
									<span class="food-orice">￥{{goods.price}}元</span>
								</li>
							</ul>
						</div>
					</el-tab-pane>
					<el-tab-pane label='小食'>
						<!-- 商品列表 -->
						<div>
							<ul class="cook-list">
								<li v-for='goods in type1Goods' @click='addOrderList(goods)'>
									<span class="food-img"><img :src="goods.goodsImg" width="100%"></span>
									<span class="food-name">{{goods.goodsName}}</span>
									<span class="food-orice">￥{{goods.price}}元</span>
								</li>
							</ul>
						</div>
					</el-tab-pane>
					<el-tab-pane label='饮料'>
						<!-- 商品列表 -->
						<div>
							<ul class="cook-list">
								<li v-for='goods in type2Goods' @click='addOrderList(goods)'>
									<span class="food-img"><img :src="goods.goodsImg" width="100%"></span>
									<span class="food-name">{{goods.goodsName}}</span>
									<span class="food-orice">￥{{goods.price}}元</span>
								</li>
							</ul>
						</div>
					</el-tab-pane>
					<el-tab-pane label='套餐 '>
						<!-- 商品列表 -->
						<div>
							<ul class="cook-list">
								<li v-for='goods in type3Goods' @click='addOrderList(goods)'>
									<span class="food-img"><img :src="goods.goodsImg" width="100%"></span>
									<span class="food-name">{{goods.goodsName}}</span>
									<span class="food-orice">￥{{goods.price}}元</span>
								</li>
							</ul>
						</div>
					</el-tab-pane>
				</el-tabs>
			</div>
		</el-col>
	</el-row>
</template>

<script>
import axios from 'axios';
export default {
  	name: 'pos',
  	data(){
  		return {
	  		// 点餐数据
	  		tableData:[
		  		// {
		    //       goodsName: '可口可乐',
		    //       price: 8,
		    //       count:1
		    //     }, {
		    //       goodsName: '香辣鸡腿堡',
		    //       price: 15,
		    //       count:1
		    //     }, {
		    //       goodsName: '爱心薯条',
		    //       price: 8,
		    //       count:1
		    //     }, {
		    //       goodsName: '甜筒',
		    //       price: 8,
		    //       count:1
		    //     }
		    ],
		    greatGoods:[],
	    	type0Goods:[],
	    	type1Goods:[],
	    	type2Goods:[],
	    	type3Goods:[],
	    	totalMoney:0,
	    	totalCount:0
  		}
  	},
  	created:function(){
  		// axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
  		// .then(response=>{
	  	// 	if (response.status!=200) {
	  	// 		alert('网络错误')
	  	// 	} else {
	  	// 		this.greatGoods=response.data
	  	// 	}
	  	// }).catch(err=>{
	  	// 	console.log(err)
	  	// 	alert('网络错误')
	  	// })
  		// axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods')
  		// .then(response=>{
	  	// 	if (response.status!=200) {
	  	// 		alert('网络错误')
	  	// 	} else {
	   //       	this.type0Goods=response.data[0];
	   //       	this.type1Goods=response.data[1];
	   //       	this.type2Goods=response.data[2];
	   //       	this.type3Goods=response.data[3];
	  	// 	}
	  	// }).catch(err=>{
	  	// 	console.log(err)
	  	// 	alert('网络错误')
	  	// })
	  	let data1=[
	  		{
				"goodsId": 1,
				"goodsName": "香辣鸡腿堡",
				"price": 18
			},
			{
				"goodsId": 2,
				"goodsName": "田园鸡腿堡",
				"price": 15
			},
			{
				"goodsId": 3,
				"goodsName": "和风汉堡",
				"price": 15
			},
			{
				"goodsId": 4,
				"goodsName": "大包薯条",
				"price": 18
			},
			{
				"goodsId": 5,
				"goodsName": "脆皮炸鸡腿",
				"price": 20
			},
			{
				"goodsId": 6,
				"goodsName": "魔法鸡块",
				"price": 20
			},
			{
				"goodsId": 7,
				"goodsName": "可乐大杯",
				"price": 10
			},
			{
				"goodsId": 8,
				"goodsName": "雪顶咖啡",
				"price": 18
			},
			{
				"goodsId": 9,
				"goodsName": "儿童欢乐套餐",
				"price": 25
			},
			{
				"goodsId": 10,
				"goodsName": "快乐全家桶",
				"price": 99
			}
		]
	  	let data2=[
			[{
					"goodsId": 1,
					"goodsImg": "http://www.liuzhuocheng.com/hams/1.png",
					"goodsName": "香辣鸡腿堡",
					"price": 18
				},
				{
					"goodsId": 2,
					"goodsImg": "http://www.liuzhuocheng.com/hams/2.png",
					"goodsName": "田园鸡腿堡",
					"price": 15
				},
				{
					"goodsId": 3,
					"goodsImg": "http://www.liuzhuocheng.com/hams/4.png",
					"goodsName": "和风汉堡",
					"price": 15
				}
			],
			[{
					"goodsId": 4,
					"goodsImg": "http://www.liuzhuocheng.com/hams/11.png",
					"goodsName": "大包薯条",
					"price": 18
				},
				{
					"goodsId": 5,
					"goodsImg": "http://www.liuzhuocheng.com/hams/5.png",
					"goodsName": "脆皮炸鸡腿",
					"price": 20
				},
				{
					"goodsId": 6,
					"goodsImg": "http://www.liuzhuocheng.com/hams/6.png",
					"goodsName": "魔法鸡块",
					"price": 20
				}
			],
			[{
					"goodsId": 7,
					"goodsImg": "http://www.liuzhuocheng.com/hams/7.png",
					"goodsName": "可乐大杯",
					"price": 10
				},
				{
					"goodsId": 8,
					"goodsImg": "http://www.liuzhuocheng.com/hams/8.png",
					"goodsName": "雪顶咖啡",
					"price": 18
				}
			],
			[{
					"goodsId": 9,
					"goodsImg": "http://www.liuzhuocheng.com/hams/12.png",
					"goodsName": "儿童欢乐套餐",
					"price": 25
				},
				{
					"goodsId": 10,
					"goodsImg": "http://www.liuzhuocheng.com/hams/3.png",
					"goodsName": "快乐全家桶",
					"price": 99
				}
			]
		]
	  	this.greatGoods=data1
	  	this.type0Goods=data2[0]
		this.type1Goods=data2[1]
		this.type2Goods=data2[2]
		this.type3Goods=data2[3]
  	},
  	mounted:()=>{
  		var orderHeight=document.body.clientHeight
  		document.getElementById('order-list').style.height=orderHeight+'px'
  	},
  	methods:{
  		// 添加商品
  		addOrderList(goods){

  			//检索商品是否已经在列表中
  			let isHave=false
  			for (let i=0;i<this.tableData.length;i++) {
  				if (this.tableData[i].goodsId==goods.goodsId) {
  					isHave=true
  				}
  			}

  			// 根据检索结果写业务逻辑
  			if (isHave) {
  				//改变列表中商品数量
  				let arr=this.tableData.filter(o=>o.goodsId==goods.goodsId)
  				arr[0].count++
  			} else {
  				let newGoods={
  					goodsId:goods.goodsId,
  					goodsName:goods.goodsName,
  					price:goods.price,
  					count:1
  				}
  				this.tableData.push(newGoods)
  			}

  			this._getAllMoney()
  		},
  		// 删除单个商品
  		delSingleGoods(goods){
  			this.tableData=this.tableData.filter(o=>o.goodsId!=goods.goodsId)
  			this._getAllMoney()
  		},
  		// 删除全部商品
  		delAllGoods(){
  			this.tableData=[]
  			this.totalCount=this.totalMoney=0
  		},
  		// 模拟结账
  		checkOut(){
  			if (this.totalCount!=0) {
  				this.tableData=[]
  				this.totalCount=this.totalMoney=0
  				this.$message({
  					message:'结账成功,因为有你,我们又成交了一份订单',
  					type:'success'
  				})
  			} else {
  				this.$message.error('空订单不能结账.老板了解你急切的心情')
  			}
  		},
  		// 统计数量和金额
  		_getAllMoney(){
  			//清零统计信息,重新统计
  			this.totalMoney=this.totalCount=0
  			//计算统计金额
  			this.tableData.forEach((element)=>{
  				this.totalCount+=element.count
  				this.totalMoney+=element.price*element.count
  			})
  		}
  	}
}
</script>

<style scoped>
	/*中栏*/
	.pos-order {
		background-color: #f9fafc;
		border-right: 1px solid #c0ccda;
		overflow-y: auto;
	}
	.mid-tab {
		padding: 10px;
	}
	.total-div {
		padding: 10px;
		background: #fff;
		border-bottom: 1px solid #d3dce6;
	}
	/*中栏底部三个操作按钮*/
	.div-btn {
		margin-top: 20px;
	}


	/*右栏*/
	/*热销商品*/
	.title {
		height: 20px;
		border-bottom: 1px solid #d3dce6;
		background-color: #f9fafc;
		padding: 10px;
		text-align: left;
	}
	.great-goods-list ul li {
		list-style: none;
		float: left;
		border: 1px solid #e5e9f2;
		padding: 10px;
		margin: 10px;
		background-color: #fff;
		cursor: pointer;
	}
	.o-price {
		color: #58b7ff;
	}

	/*商品分类菜单*/
	.goods-type {
		clear:both;
		padding: 10px;
	}
	.cook-list li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor: pointer;
   	}
   	.cook-list li span{
        display: block;
        float:left;
   	}
   	.food-img{
       width: 40%;
   	}
   	.food-name{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
       width: 50%;
   	}
   	.food-price{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   	}
</style>


